import React from 'react';
import styles from '../index.scss';
import { List, message, Avatar, Spin, Divider } from 'antd';
import reqwest from 'reqwest';
import axios from '@/request/axiosConfig';
import api_logs from '@/request/api/api_logs';
import moment from 'moment';
import './operationLog.css';

import InfiniteScroll from 'react-infinite-scroller';

const fakeDataUrl =
  'https://randomuser.me/api/?results=5&inc=name,gender,email,nat&noinfo';

export default class OperationLog extends React.Component {
  state = {
    data: [],
    loading: false,
    hasMore: true
  };

  async componentDidMount() {
    await this.getOperationLogs();

    // this.fetchData(res => {
    //   this.setState({
    //     data: res.results
    //   });
    // });
  }

  // 获取所有操作日志
  getOperationLogs = async () => {
    const { id: uid } = JSON.parse(localStorage.getItem('userInfo'));
    const res = await axios({
      url: api_logs.getOperationLogs,
      method: 'post',
      data: {
        uid
      }
    });
    console.log('res', res);
    await this.setState(
      {
        data: res.data.data
      },
      () => {
        console.log(this.state);
      }
    );
  };

  fetchData = callback => {
    reqwest({
      url: fakeDataUrl,
      type: 'json',
      method: 'get',
      contentType: 'application/json',
      success: res => {
        callback(res);
      }
    });
  };

  handleInfiniteOnLoad = async () => {
    let { data } = this.state;
    this.setState({
      loading: true
    });
    if (data.length > 6) {
      message.warning('正在加载...');
      this.setState({
        hasMore: false,
        loading: false
      });
      return;
    }
    const { id: uid } = JSON.parse(localStorage.getItem('userInfo'));
    const res = await axios({
      url: api_logs.getOperationLogs,
      method: 'post',
      data: {
        uid
      }
    });
    data = data.concat(res.data.data);
    await this.setState({
      data,
      loading: false
    });
    console.log('res----2', res);

    // this.fetchData(res => {
    //   data = data.concat(res.results);
    //   this.setState({
    //     data,
    //     loading: false
    //   });
    // });
  };

  render() {
    return (
      <div className='demo-infinite-container'>
        <Divider orientation='left'>操作日志</Divider>
        <InfiniteScroll
          initialLoad={true}
          pageStart={0}
          loadMore={this.handleInfiniteOnLoad}
          hasMore={!this.state.loading && this.state.hasMore}
          useWindow={false}
        >
          <List
            dataSource={this.state.data}
            renderItem={item => {
              return (
                <List.Item key={item.id}>
                  <List.Item.Meta
                    avatar={
                      <Avatar src='https://zos.alipayobjects.com/rmsportal/ODTLcjxAfvqbxHnVXCYX.png' />
                    }
                    title={<a href='https://ant.design'>批量{item.type}主机</a>}
                    description={`受影响主机ID：${item.hids}`}
                  />
                  {/* 操作时间 */}
                  <div>
                    {moment(item.createdAt).format('YYYY-MM-DD HH:mm:ss')}
                  </div>
                </List.Item>
              );
            }}
          >
            {this.state.loading && this.state.hasMore && (
              <div className='demo-loading-container'>
                <Spin />
              </div>
            )}
          </List>
        </InfiniteScroll>
      </div>
    );
  }
}
